import React, { useEffect, useMemo, useState } from "react";
import { Col, Container, Row } from "react-bootstrap";
import { useForm } from "react-hook-form";
import { ReactComponent as Search } from "assets/svgs/search.svg";
import Select from "react-select";
// needed styles
import styles from "./index.module.scss";
import { Link, useNavigate } from "react-router-dom";
import { getAllSpecialty, getSubscritionTypes } from "api/employee";
import { useDispatch, useSelector } from "react-redux";
import { FormattedMessage } from "react-intl";
import { fetchCVs } from "store/empolyee/slice";

const SearchForEemployee = () => {
  const {
    meta: { locale },
    authentication: { user, isLoggedIn },
    empolyee: { specialties, nationalties },
  } = useSelector((state) => state);
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const { handleSubmit, register } = useForm({
    defaultValues: {},
  });

  const submitHandler = (data) => {
    user?.search_type_id && isLoggedIn
      ? dispatch(fetchCVs({ ...data, callBack: () => navigate("/cvs") }))
      : navigate("/employmentSubscription");
  };

  const specialtiesOptions = useMemo(
    () => [
      ...specialties.map((specialty) => ({
        label: locale === "ar" ? specialty.name_ar : specialty.name_en,
        value: specialty._id,
      })),
    ],
    [specialties, locale]
  );
  return (
    <div className={styles.body}>
      <Container>
        <Row>
          <Col xs={12} className={styles.headers}>
            <h3>التوظيف بالخارج</h3>
            <p>نقدم خدمات التوظيف بالخارج لكل من الشركات والأفراد</p>
          </Col>
          <Col xs={12}>
            <Row>
              <Col xs={12} md={3} />
              <Col xs={6} md={3}>
                <Link to={"/docmentrequest"} className={styles.tabBtn}>
                  توثيق الأوراق الرسمية
                  <svg
                    width="32"
                    height="32"
                    viewBox="0 0 20 20"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      fill="white"
                      d="M16 1H4a1 1 0 0 0-1 1v16a1 1 0 0 0 1 1h12a1 1 0 0 0 1-1V2a1 1 0 0 0-1-1zm-3 14H7v-2h6v2zm0-4H7V9h6v2zm0-4H7V5h6v2z"
                    />
                  </svg>
                </Link>
              </Col>
              <Col xs={6} md={3}>
                <Link to={"/createCv"} className={styles.tabBtn}>
                  التقديم على وظيفة
                  <svg
                    width="32"
                    height="32"
                    viewBox="0 0 32 32"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <g
                      fill="white"
                      stroke="#ffaa03"
                      stroke-linejoin="round"
                      stroke-miterlimit="10"
                      strokeWidth="2"
                    >
                      <path d="M1 7h30v24H1z" />
                      <path d="M31 7H1v6a4 4 0 0 0 4 4h22a4 4 0 0 0 4-4V7zM8 7V3a2 2 0 0 1 2-2h12a2 2 0 0 1 2 2v4" />
                      <path d="M16 21a2 2 0 0 1-2-2v-4a2 2 0 1 1 4 0v4a2 2 0 0 1-2 2zm2-4h-4" />
                    </g>
                  </svg>
                </Link>
              </Col>
              <Col xs={12} md={3} />
            </Row>
          </Col>
          <Col xs={12} className={styles.formContainer}>
            <form onSubmit={handleSubmit(submitHandler)}>
              <Row className={styles.form}>
                <Col xs={12}>
                  <h3>البحث عن موظفين</h3>
                </Col>
                <Col xs={12}>
                  <div className={`${styles["search"]}`}>
                    <select
                      type="search"
                      name="specialty_id"
                      {...register("specialty_id", { required: true })}
                    >
                      <option value={null} selected={true}>
                        ابحث عن المهنه
                      </option>
                      {specialtiesOptions.map((specialty) => (
                        <option value={specialty.value}>
                          {specialty.label}
                        </option>
                      ))}
                    </select>
                    {/* <input
                      type="text"
                      placeholder="ابحث عن المهنه"
                      name="search"
                      {...register("search", { required: true })}
                    /> */}

                    <Search />
                  </div>
                </Col>
                <Col xs={12}>
                  <div className={`${styles["select"]}`}>
                    <select
                      type="search"
                      {...register("country_id", { required: true })}
                    >
                      <option value="">اختر الدوله</option>
                      {nationalties.map((nationalty) => (
                        <option key={nationalty.id}>
                          {locale === "ar"
                            ? nationalty?.["name_ar"]
                            : nationalty?.["name_en"]}
                        </option>
                      ))}
                    </select>
                  </div>
                </Col>
                <Col xs={12}>
                  <button className={styles["button"]} type="submit">
                    {user?.search_type_id && isLoggedIn ? (
                      <FormattedMessage id="search" />
                    ) : (
                      <FormattedMessage id="subscribeFirst" />
                    )}
                  </button>
                </Col>
              </Row>
            </form>
          </Col>
        </Row>
      </Container>
    </div>
  );
};

export default SearchForEemployee;
